@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Mulish:wght@400;700&display=swap);
body {
  background-color: black;
  position: relative;
  overflow-x: hidden;
  margin: 0;
  font-family: 'Inter', 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: black;
}

.slide-enter,
.slide-exit {
  transition: -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}
.slide-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%)
}
.slide-enter.slide-enter-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.slide-exit {
  position: absolute;
  top: 45px;
  width: 100%;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.slide-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}


.Home {
    /*background: black;*/
    color: white;
    background-image: url("https://media.discordapp.net/attachments/966328852957769778/1154436433956847636/Untitled.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: 50% 100%;
    height: calc(100% - 45px);
    width: 100vw;
    position: fixed;
}
h1 {
    text-align: center;
    font-size: 200px;
}
p {
    text-align: center;
    font-size: 60px;
}
.SkinComponent {
    margin-top: 40px;
    margin-left: 20px;
    margin-right: 20px;
    width: 440px;
    height: 300px;
    background-color: #020202;
    border-radius: 8px;
    -webkit-filter: drop-shadow(0px 0px 1px rgb(255,255,255, 0.4));
            filter: drop-shadow(0px 0px 1px rgb(255,255,255, 0.4));
    transition: -webkit-filter 0.3s;
    transition: filter 0.3s;
    transition: filter 0.3s, -webkit-filter 0.3s;
}
.SkinPreview {
    position: relative;
    width: 400px;
    height: 225px;
    border-radius: 4px;
    padding-top: 12px;
    transition: 1s;
}
.SkinComponent:hover {
    -webkit-filter: drop-shadow(1px 2px 4px rgb(255,255,255, 0.8));
            filter: drop-shadow(1px 2px 4px rgb(255,255,255, 0.8));
    cursor: pointer;

}
.SkinHeader {
    position: relative; 
    height: 45px;
}
.SkinName {
    color: white;
    float: left;
    padding-left: 20px;
    padding-top: 12px;
    font-weight: bold;
    font-size: 20px;
}

.SkinVersion {
    float: left;
    padding-left: 8px;
    padding-top: 18px;
    color: gray;
    font-size: 16px;
}
.SkinYear {
    float: right;
    padding-right: 20px;
    padding-top: 18px;
    color: gray;
    font-size: 16px;
}
.Skins {
    display: grid;
    justify-content: center;
    justify-items: center;
    grid-template-columns: repeat(auto-fit, 500px);
    margin: 5% 10% 0% 10%;
    padding-bottom: 10%;
    background: black;
}
.Navbar {
    width: 96vw;
    padding-left: 2vw;
    padding-right: 2vw;
    height: 45px;
    display: flex;
    color: #AAAAAA;
    align-items: center;
    justify-content: space-between;
}
.NavbarContainer {
    display: flex;
    flex-direction: row;
}
.NavbarBadge {
    font-weight: bold;
    font-size: 30px;
    color: white;   
    padding-right: 40px;
}
.NavbarButton {
    padding-right: 8px;
    margin-left: 8px;
    margin-right: 8px;
    margin-top: auto;
    height: 25px;
    line-height: 20px;
    border-radius: 5px;
    transition: 0.3s;
    font-size: 20px;
}
.NavbarButton:hover {
    color: white;
    cursor: pointer;
}
a {
    color: inherit;
    text-decoration: inherit;
}
.NavbarIcon {
    padding-top: 16px;
    padding-left: 14px;
    height: 25px;
    width: 25px;
    opacity: 0.67;
    transition: 0.3s;
}
.NavbarIcon:hover {
    opacity: 1;
}


